let validator = {
    userName: [
        {
            type: 'isRequired',
            message: ''
        },
        {
            type: 'isMobilePhone',
            message: ''
        }
    ],
    password: [
        {
            type: 'isFilled',
            message: ''
        }
    ],
    rePassword: [
        {
            type: 'isFilled',
            message: ''
        },
        {
            message: '',
            method: function (value, rule, options) {
                return value === options.compIns.$checkParent.password;
            }
        }
    ]
}

export default validator



