let validator = {
    userName: [
        {
            type: 'isRequired',
            message: ''
        },
        {
            type: 'isMobilePhone',
            message: ''
        }
    ],
    password: [
        {
            type: 'isRequired',
            message: ''
        }
    ]
}

export default validator



