import Base from '@/base/base.vue'
import CommonCache from '@/cache/common/cache'
import {
    Input as IInput,
    Button as IButton,
    Row as IRow,
    Col as ICol,
    Tabs as ITabs,
    TabPane as ITabPane
} from 'iview'
/**
 * 
 * @param    {String}           repoName                    git
 * @param    {String}           iconPrefix                  |git
 * @param    {String}           repoDescription             git
 * @return   {void}
 */
export default Base.extend({
    data () {
        return {
            config: [
                {
                    name: 'MONGODB_NAME',
                    value: 'iconRepo',
                    description: 'mongo',
                    necessary: true
                },
                {
                    name: 'MONGODB_HOST',
                    value: '127.0.0.1',
                    description: 'mongoIP',
                    necessary: true
                },
                {
                    name: 'MONGODB_PORT',
                    value: '27017',
                    description: 'mongo',
                    necessary: true
                },
                {
                    name: 'MONGODB_USERNAME',
                    value: '',
                    description: 'mongo',
                    necessary: true
                },
                {
                    name: 'MONGODB_PASSWORD',
                    value: '',
                    description: 'mongo',
                    necessary: true
                },
                {
                    name: 'REDIS_HOST',
                    value: '127.0.0.1',
                    description: 'redisIP',
                    necessary: true
                },
                {
                    name: 'REDIS_PORT',
                    value: '6379',
                    description: 'redis',
                    necessary: true
                },
                {
                    name: 'REDIS_PASSWORD',
                    value: '',
                    description: 'redis',
                    necessary: true
                },
                {
                    name: 'productHost',
                    value: 'icon.bolin.site',
                    description: 'host',
                    necessary: true
                },
                {
                    name: 'productType',
                    value: 'default',
                    description: '',
                    necessary: true
                }
            ],
            login: {
                index: '',
                config: ''
            },
            upload: {
                index: '',
                config: ''
            },
            uploadPlaceholder: `
                // index.js
    
                let config = require('./config');
                let qiniu = require('qiniu');
                
                class QiniuUpload {
                    async upload (dirPath) {
                        let fontMap = await this.uploadFonts(dirPath);
                        // fontcss
                        let cssContent = await this.replaceFontsInCss(dirPath, fontMap);
                        let cssUrl = await this.uploadCss(dirPath, cssContent);
                        
                        // 
                        return {
                            url: cssUrl, // 
                            cssContent: cssContent // 
                        };
                    }
                }
                
                let uploadIns = new QiniuUpload();
                module.exports = uploadIns.upload.bind(uploadIns);
                `,
            loginPlaceholder: `
                // index.js
    
                require('request');
                let rp = require('request-promise');
                let config = require('./config');
                
                class GithubOpenIdLogin {
                    async login (ctx) {
                        return this.getUserBaseInfo(ctx);
                    }
                
                    async getUserBaseInfo (ctx) {
                        // your code
                        
                        // login 
                        return {
                            userName: tokenInfo.sub, // 
                            password: tokenInfo.sub,
                            email: openIdUserInfo.email,
                            nickName: openIdUserInfo.nickname,
                            fullName: openIdUserInfo.fullname
                        }
                    }
                }
                
                let loginIns = new GithubOpenIdLogin();
                module.exports = loginIns.login.bind(loginIns);
                `,
            configPlaceholder: `
                let pe = process.env;
                
                module.exports = {
                    accessKey: pe.QINIU_UPLOAD_ACCESS_KEY,
                    secretKey: pe.QINIU_UPLOAD_SECRET_KEY,
                    bucket: pe.QINIU_UPLOAD_BUCKET,
                    cdnHost: pe.QINIU_UPLOAD_CDN_HOST
                };`
        }
    },
    components: { IInput, IButton, IRow, ICol, ITabs, ITabPane },
    methods: {
        addItem () {
            this.config.push({
                name: '',
                value: '',
                description: ''
            })
        },
        deleteItem (index) {
            this.config.splice(index, 1);
        },
        submit () {
            let data = {
                config: this.config.filter(item => {
                    return item.value !== '';
                }),
                login: this.login,
                upload: this.upload
            };
            this.cache.installApplication({
                data: data,
                onload: result => {
                    if (result.result) {
                        let seconds = 5;
                        let timer = setInterval(() => {
                            if (seconds > 0) {
                                seconds--
                            } else {
                                clearInterval(timer);
                                location.href = location.protocol + '//' + location.host;
                            }
                        }, 1000)
                        this.$Modal.success({
                            title: '',
                            content: `${seconds}`
                        })
                    }
                }
            })
        }
    },
    created: function () {
        this.cache = new CommonCache({});
    }
})



