import Base from '@/base/base.vue'
/**
 * operatorOptions                              
 * operatorOptions.allowCopy=false              
 * operatorOptions.allowEdit=false              
 * operatorOptions.allowDelete=false            
 * operatorOptions.allowDownload=false          
 * iconActiveSelected=false                     ,operatorOptions
 * selected=false                               
 * isEdit=false                                 
 *
 */
export default Base.extend({
    data () {
        return {
            title: 'Welcome to Icon App',
            selected: false,
            isEdit: false,
            preIconName: ''
        }
    },
    computed: {
        userInfo () {
            return this.$store.state.webUser
        },
        isOwner () {
            return this.icon.ownerId === this.userInfo.userId
        }
    },
    props: {
        icon: Object,
        iconOperatorOptions: {
            type: Object,
            default: function () {
                return {
                    allowCopy: true,
                    allowEdit: true,
                    allowDelete: true,
                    allowDownload: true,
                    allowCollect: true
                }
            }
        },
        copyTextPrefix: {
            type: String,
            default: ''
        },
        iconActiveSelected: {
            type: Boolean,
            default: false
        },
        selectedAllIconFlag: {
            type: Boolean,
            default: false
        }
    },
    watch: {
        selectedAllIconFlag: function (newVal, oldVal) {
            if (newVal === oldVal) {
                return;
            }
            this.selectedIcon(newVal)
        }
    },
    methods: {
        /**
         * 
         *
         * @param    {Integer}        iconId            Id
         * @return   {void}
         */
        deleteIcon () {
            let that = this
            this.$Modal.confirm({
                content: '',
                onOk () {
                    that.$emit('delete', that.icon.iconId)
                }
            })
        },
        /**
         * 
         *
         * @param    {String}        iconName            
         * @return   {void}
         */
        copyIconName () {
            this.$emit('copy', this.icon.iconName)
        },
        /**
         * collect icon | add to draft
         *
         * @return   {void}
         */
        collectIcon () {
            this.$emit('collect', this.icon)
        },
        /**
         * icon
         *
         * @param    {Object}        icon            
         * @return   {void}
         */
        changeEditStatus () {
            this.isEdit = true
            // 
            this.preIconName = this.icon.iconName
            this.$emit('editStatus', this.isEdit)
        },
        /**
         * 
         *
         * @param    {Object}        icon            
         * @return   {void}
         */
        editIcon () {
            this.isEdit = false
            if (this.preIconName === this.icon.iconName) {
                return
            }
            this.$emit('edit', this.icon)
        },
        /**
         * 
         *
         * @param    {Boolean}        selected            
         * @return   {void}
         */
        selectedIcon (selected) {
            if (!this.iconActiveSelected) {
                return
            }
            this.selected = (selected === undefined) ? !this.selected : selected
            this.$emit(this.selected ? 'selected' : 'unselected', this.icon)
        },
        /**
         * 
         *
         * @param    {Object}        icon            
         * @return   {void}
         */
        downloadIcon () {
            this.$emit('download', this.icon)
        }
    },
    created () {}
})



