import Base from '@/base/base.vue'
import iconUrlConfig from '@/cache/icon/config'
import {
    Upload as IUpload
} from 'iview'
export default Base.extend({
    data () {
        return {
            title: 'Welcome to Icon App',
            uploadIconPath: iconUrlConfig['icon-draft-add'].url,
            totalCount: 0
        }
    },
    components: { IUpload },
    methods: {
        /**
         * 
         *
         * @param    {Object}           response                    
         * @param    {Object}           file                        
         * @param    {Array}            fileList                    
         * @return   {void}
         */
        uploadSuccess (response, file, fileList) {
            if (response && response.code !== 200) {
                this.$Message.error(response.message)
                return
            }
            this.$emit('onaddicon')
        },
        /**
         * 
         *
         * @param    {Object}           file                        
         * @return   {void}
         */
        beforeUpload (file = {}) {
            // 
            // file.name = file.name.replace('.svg', '');
        },
        /**
         * icon
         *
         */
        uploadFail (error, file, fileList) {
            this.$Message.error(error)
            console.log(error)
        },
        /**
         * 
         *
         */
        uploadFormatError (file, fileList) {
            this.$Message.error('svg')
        }
    }
})



